import React, { useEffect } from "react"
import { graphql, useStaticQuery } from "gatsby"

export const GlobalStateContext = React.createContext()
export const GlobalDispatchContext = React.createContext()

const initialState = {
  selectedVersion: null,
  versions: JSON.parse(process.env.GATSBY_DEPLOY_BRANCHES).map(version => ({
    ...version,
    versionPrefix:
      version.name === process.env.GATSBY_MAIN_BRANCH_NAME ? "" : version.name,
  })),
  currentLocation: null,
  sitePages: [],
  searchIndex: null,
  searchStore: null,
}

function reducer(state, action) {
  switch (action.type) {
    case "UPDATE_SELECTED_API_VERSION": {
      const routePrefix = action.payload.pathname.split("/")[1]

      // gets the branch based on the url prefix with fallback to the default branch
      const version =
        state.versions.find(branch => branch.name === routePrefix) ??
        state.versions.find(
          branch => branch.name === process.env.GATSBY_MAIN_BRANCH_NAME
        )

      return { ...state, selectedVersion: version }
    }
    case "UPDATE_CURRENT_LOCATION": {
      return { ...state, currentLocation: action.payload }
    }
    case "SET_SITE_PAGES": {
      return { ...state, sitePages: action.payload }
    }
    case "SET_SEARCH_INDEX": {
      return { ...state, searchIndex: action.payload }
    }
    case "SET_SEARCH_STORE": {
      return { ...state, searchStore: action.payload }
    }
  }
}

const GlobalContextProvider = ({ children }) => {
  const queryData = useStaticQuery(graphql`
    query SitePages {
      allSitePage {
        nodes {
          path
        }
      }
      allMarkdownRemark(
        filter: {
          fileAbsolutePath: {
            regex: ".*/(gatsby-source-git)/.*/(home-pages)/(TOC.md)/ig"
          }
        }
      ) {
        edges {
          node {
            parent {
              ... on File {
                gitRemote {
                  ref
                }
                relativePath
              }
            }
            htmlAst
            fileAbsolutePath
          }
        }
      }
    }
  `)

  const [state, dispatch] = React.useReducer(reducer, initialState)

  useEffect(() => {
    const pages = queryData.allSitePage.nodes.map(page => page.path)
    dispatch({ type: "SET_SITE_PAGES", payload: pages })
  }, [])

  return (
    <GlobalStateContext.Provider value={state}>
      <GlobalDispatchContext.Provider value={dispatch}>
        {children}
      </GlobalDispatchContext.Provider>
    </GlobalStateContext.Provider>
  )
}

export default GlobalContextProvider
